body {
  box-sizing: border-box;
  background-image: url(/assets/negative-space-black-forest-dawn.jpg);
  background-repeat: no-repeat, repeat;
  color: white;
  height: 250vh;
  margin: 0;
  padding: 0;
}

nav {
  display: flex;
  align-items: center;
  justify-content: end;
}

nav ul li {
  list-style-type: none;
  display: inline-block;
  padding: 10px 20px;
}

nav ul li a {
  color: white;
  text-decoration: none;
}

p {
  display: flex;
  color: white;
  font-size: 20px;
  align-items: center;
  justify-content: center;
  min-height: 130px;
}

svg {
  position: relative;
  border-radius: 50%;
  color: white;
  justify-content: center;
  align-items: center;
  text-align: center;
  top: 80px;
  left: 550px;
  margin: auto;
}

.text {
  background-color: gray;
  color: black;
  top: 320px;
  box-shadow: 0 10px 30px -10px rgb(0, 0, 0);
}

.card--light {
  background: #eee;
  border-radius: 50%;
  width: 20%;
}

h6 {
  font-size: 13px;
  background-color: rgb(160, 160, 160);
  color: black;
  margin: 2em;
  max-width: 400px;
  padding: 2em;
  box-shadow: 0 10px 30px -10px rgb(0, 0, 0);
  left: 200px;
}

h5 {
  display: flex;
  position: relative;
  font-size: 25px;
  align-items: center;
  color: black;
  text-align: center;
  top: 300px;
  left: 580px;
}

aside {
  display: flex;
  flex-wrap: wrap;
}

.nummer {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  border-radius: 50%;
  width: 50%;
}

.bild {
  display: flex;
  flex-direction: row;
  border-radius: 50%;
  height: 250px;
  width: 250px;
  padding: 200px;
  flex-wrap: wrap;
}

.card--dark {
  display: flex;
  position: relative;
  width: 350px;
  height: 30px;
  margin: 0;
  bottom: 190px;
  left: 120px;
}

.namn {
  display: flex;
  position: relative;
  justify-content: center;
  top: 210px;
  color: black;
}

a {
  position: relative;
  text-decoration: none;
  font-family: "poppins", sans-serif;
  color: #a0a0a0;
  font-size: 15px;
  letter-spacing: 0.5px;
  padding: 0 10px;
}

a:after {
  content: "";
  background-color: white;
  height: 3px;
  width: 0;
  left: 0;
  bottom: -10px;
}

a:hover:after {
  width: 100%;
}

html {
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
  color: black;
}

#down {
  margin-top: 18%;
  padding-bottom: 25%;
}

.instagram {
  top: 2185px;
  left: 1000px;
  color: white;
}

.ins {
  top: 1500px;
  left: 500px;
  color: black;
}

.twitter {
  position: absolute;
  top: 2015px;
  left: 1000px;
  height: 50px;
  width: 50px;
  color: rgb(255, 255, 255);
}

footer {
  display: flex;
  background-color: black;
  height: 200px;
}
footer .instagram {
  display: flex;
  position: relative;
  width: 100px;
  background-color: green;
}

